* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  text-align: center;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  background-color: #004ab2;
  padding: 15px;
  position: fixed;
}

.input-wrapper {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

.input-wrapper input {
  height: 35px;
  width: 300px;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 3px;
  padding-left: 10px;
}

.input-wrapper input:active, .input-wrapper input:focus {
  outline: none;
}

.input-wrapper button {
  height: 35px;
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 3px;
  position: absolute;
  top: 0;
  right: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  padding-top: 95px;
  display: flex;
}

.photo-card {
  margin-bottom: 20px;
}

.photo-card img {
  width: 350px;
  display: block;
}

.info {
  background-color: rgba(195, 195, 195, .34);
  border-bottom: 1px solid rgba(128, 128, 128, .52);
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 14px;
}

.load-more {
  width: 150px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #004ab2;
  border: rgba(0, 0, 0, 0);
  border-radius: 20px;
  margin: 20px auto;
  font-weight: 700;
  transition: all .3s;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
}

.load-more:hover {
  box-shadow: inset 0 30px 60px -12px rgba(50, 50, 93, .25), inset 0 18px 36px -18px rgba(0, 0, 0, .3);
}

